<template>
  <div class="lyrics-container">
      <!-- 小喇叭播放按钮 -->
      <button @click="toggleMusic" class="play-music-button">
          <i class="fa-solid fa-volume-high">播放</i>
      </button>

      <!-- 键盘输入按钮 -->
      <button @click="toggleKeyboard" class="keyboard-button">
          <i class="fa-solid fa-keyboard"></i>
      </button>

      <div v-if="isKeyboardOpen" class="keyboard-input">
          <div class="input-group">
              <input v-model="inputText"
                     placeholder="输入新的第11和12个字"
                     @keyup.enter="updateLyrics"
                     class="lyric-input">
          </div>
          <div class="button-group">
              <button @click="updateLyrics" class="confirm-btn">确认修改</button>
              <button @click="toggleKeyboard" class="cancel-btn">关闭</button>
          </div>
      </div>

      <div class="lyrics-content">
          <div class="row" v-for="(line, index) in lyrics" :key="index">
              <img
                  class="notation-image"
                  :src="line.image"
                  :style="{ marginLeft: line.imageOffset + 'px' }"
              >
              <p class="text">
                  <span
                      v-for="(charObj, charIndex) in line.text"
                      :key="charIndex"
                      class="char"
                      :style="{ transform: `translateX(${charObj.offsetX}px)` }"
                  >
                      {{ charObj.char }}
                  </span>
              </p>
          </div>
      </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      lyrics: [
        {
          image: 'xnhrow1.png',
          imageOffset: -20,
          text: [
            { char: '新', offsetX: -141, playTime: 4.9, instrumentX: 341, instrumentY: 455 },
            { char: '年', offsetX: -152, playTime: 5.30, instrumentX: 381, instrumentY: 455 },
            { char: '好', offsetX: -153, playTime: 5.8, instrumentX: 446, instrumentY: 455 },
            { char: '呀', offsetX: -139, playTime: 6, instrumentX: 488, instrumentY: 455 },
            { char: '新', offsetX: -95, playTime: 6.41, instrumentX: 578, instrumentY: 455 },
            { char: '年', offsetX: -105, playTime: 7, instrumentX: 686, instrumentY: 455 },
            { char: '好', offsetX: -105, playTime: 7.45, instrumentX: 815, instrumentY: 455 },
            { char: '呀', offsetX: -93, playTime: 8, instrumentX: 855, instrumentY: 455 },
            { char: '祝', offsetX: -43, playTime: 8.07, instrumentX: 922, instrumentY: 455 },
            { char: '贺', offsetX: -50, playTime: 8.50, instrumentX: 963, instrumentY: 455 },
            { char: '大', offsetX: -53, playTime: 9, instrumentX: 1053, instrumentY: 455 },
            { char: '家', offsetX: -42, playTime: 9.53, instrumentX: 1160, instrumentY: 455 },
            { char: '新', offsetX: 10, playTime: 9.53, instrumentX: 1160, instrumentY: 455 },
            { char: '年', offsetX: -2, playTime: 9.53, instrumentX: 1160, instrumentY: 455 },
            { char: '好', offsetX: -3, playTime: 9.53, instrumentX: 1160, instrumentY: 455 }
          ],
          instruments: []
        },
        {
          image: 'xnhrow2.png',
          imageOffset: -20,
          text: [
            { char: '我', offsetX: -141, playTime: 13.19, instrumentX: 333, instrumentY: 760 },
            { char: '们', offsetX: -152, playTime: 13.71, instrumentX: 445, instrumentY: 760 },
            { char: '唱', offsetX: -153, playTime: 14.17, instrumentX: 585, instrumentY: 760 },
            { char: '歌', offsetX: -139, playTime: 14.33, instrumentX: 698, instrumentY: 760 },
            { char: '我', offsetX: -95, playTime: 14.77, instrumentX: 836, instrumentY: 760 },
            { char: '们', offsetX: -105, playTime: 15.34, instrumentX: 951, instrumentY: 760 },
            { char: '跳', offsetX: -105, playTime: 15.82, instrumentX: 1090, instrumentY: 760 },
            { char: '舞', offsetX: -93, playTime: 16.21, instrumentX: 1205, instrumentY: 760 },
            { char: '祝', offsetX: -43, playTime: 16.21, instrumentX: 1205, instrumentY: 760 },
            { char: '贺', offsetX: -50, playTime: 16.21, instrumentX: 1205, instrumentY: 760 },
            { char: '大', offsetX: -53, playTime: 16.21, instrumentX: 1205, instrumentY: 760 },
            { char: '家', offsetX: -42, playTime: 16.21, instrumentX: 1205, instrumentY: 760 },
            { char: '新', offsetX: 10, playTime: 16.21, instrumentX: 1205, instrumentY: 760 },
            { char: '年', offsetX: -2, playTime: 16.21, instrumentX: 1205, instrumentY: 760 },
            { char: '好', offsetX: -3, playTime: 16.21, instrumentX: 1205, instrumentY: 760 }
          ],
          instruments: []
        }
      ],
      isPlaying: false,
      audio: new Audio('xnhbz.mp3'),
      currentTime: 0,
      isKeyboardOpen: false,
      inputText: ''
    }
  },
  methods: {
    toggleMusic () {
      if (this.isPlaying) {
        this.audio.pause()
        clearInterval(this.timer)
      } else {
        this.audio.play()
        this.timer = setInterval(() => {
          this.currentTime = this.audio.currentTime
          this.checkInstrumentPlay()
        }, 100)
      }
      this.isPlaying = !this.isPlaying
    },
    checkInstrumentPlay () {
      this.lyrics.forEach((line, lineIndex) => {
        line.text.forEach((charObj, charIndex) => {
          if (
            line.instruments &&
                      line.instruments[charIndex] &&
                      line.instrumentAudios &&
                      line.instrumentAudios[charIndex] &&
                      this.currentTime >= charObj.playTime &&
                      !charObj.hasPlayed
          ) {
            line.instrumentAudios[charIndex].play()
            charObj.hasPlayed = true
          }
        })
      })
    },
    toggleKeyboard () {
      this.isKeyboardOpen = !this.isKeyboardOpen
    },
    updateLyrics () {
      if (this.inputText.length === 2) {
        this.lyrics.forEach((line) => {
          if (line.text.length >= 12) {
            line.text[10].char = this.inputText[0]
            line.text[11].char = this.inputText[1]
          }
        })
        this.isKeyboardOpen = false
        this.inputText = ''
      }
    }
  },
  beforeDestroy () {
    clearInterval(this.timer)
  }
}
</script>

<style scoped>
.lyrics-container {
  font-family: "Courier New", Courier, monospace;
  line-height: 1.5;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.play-music-button {
  position: fixed;
  top: 20px;
  left: 20px;
  width: 44px;
  height: 44px;
  background: linear-gradient(135deg, #4a90e2 0%, #2b6cd4 100%);
  border: none;
  border-radius: 12px;
  cursor: pointer;
  box-shadow: 0 4px 15px rgba(43, 108, 212, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 1000;
}

.play-music-button:hover {
  transform: scale(1.1) rotate(8deg);
  box-shadow: 0 6px 20px rgba(43, 108, 212, 0.3);
}

.play-music-button i {
  font-size: 20px;
  color: white;
  transition: transform 0.2s;
}

.keyboard-button {
  position: fixed;
  top: 20px;
  right: 20px;
  width: 44px;
  height: 44px;
  background: linear-gradient(135deg, #6B8DD6 0%, #8E37D7 100%);
  border: none;
  border-radius: 12px;
  cursor: pointer;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 1000;
}

.keyboard-button:hover {
  transform: scale(1.1) rotate(8deg);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.3);
}

.keyboard-button i {
  font-size: 20px;
  color: white;
  transition: transform 0.2s;
}

.keyboard-input {
  position: fixed;
  top: 80px;
  right: 20px;
  background: rgba(255, 255, 255, 0.95);
  padding: 16px;
  border-radius: 16px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(8px);
  width: 280px;
  z-index: 1000;
  animation: slideIn 0.3s ease-out;
}

@keyframes slideIn {
  from {
      opacity: 0;
      transform: translateY(-10px);
  }
  to {
      opacity: 1;
      transform: translateY(0);
  }
}

.input-group {
  margin-bottom: 12px;
}

.lyric-input {
  width: 100%;
  padding: 12px;
  border: 2px solid #e0e0e0;
  border-radius: 8px;
  font-size: 14px;
  transition: all 0.3s;
}

.lyric-input:focus {
  outline: none;
  border-color: #6B8DD6;
  box-shadow: 0 0 0 3px rgba(107, 141, 214, 0.2);
}

.button-group {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 8px;
}

.confirm-btn,
.cancel-btn {
  padding: 10px 16px;
  border: none;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s;
}

.confirm-btn {
  background: linear-gradient(135deg, #6B8DD6 0%, #8E37D7 100%);
  color: white;
}

.cancel-btn {
  background: #f5f5f5;
  color: #666;
}

.confirm-btn:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(107, 141, 214, 0.3);
}

.cancel-btn:hover {
  background: #eee;
  color: #333;
}

.lyrics-content {
  width: 100%;
  max-width: 800px;
}

.row {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
}

.notation-image {
  width: 245px;
  max-width: 800px;
  margin-bottom: 10px;
  position: relative;
  left: 0;
}

.text {
  position: relative;
  letter-spacing: 0.5em;
  word-spacing: -0.4em;
  margin-left: 50px;
  display: flex;
  justify-content: center;
  white-space: pre;
  margin: 0;
}

.char {
  display: inline-block;
  min-width: 1em;
  position: relative;
  transition: transform 0.3s;
  color: #333;
}

@media (max-width: 480px) {
  .keyboard-input {
      width: calc(100% - 40px);
      right: 20px;
      left: 20px;
  }
}
</style>
