<template>
    <div class="lyrics-container">
      <!-- 播放音乐按钮 -->
      <button @click="toggleMusic" class="play-music-button">
        {{ isPlaying ? '暂停音乐' : '播放音乐' }}
      </button>
      <div class="lyrics-content">
        <div class="row" v-for="(line, index) in lyrics" :key="index">
          <img
            class="notation-image"
            :src="line.image"
            :style="{ marginLeft: line.imageOffset + 'px' }"
          >
          <p class="text">
            <span
              v-for="(charObj, charIndex) in line.text"
              :key="charIndex"
              class="char"
              :style="{ transform: `translateX(${charObj.offsetX}px)` }"
            >
              {{ charObj.char }}
            </span>
          </p>
          <!-- 乐器放置区域 -->
          <div class="instrument-boxes">
            <div
              v-for="(charObj, charIndex) in line.text"
              :key="charIndex"
              class="instrument-box"
              :style="{
                left: charObj.instrumentX + 'px',
                top: charObj.instrumentY + 'px'
              }"
              @click="placeInstrument(index, charIndex)"
            >
              <img v-if="line.instruments && line.instruments[charIndex]" :src="line.instruments[charIndex]" alt="Instrument" class="instrument-image">
            </div>
          </div>
        </div>
      </div>
      <!-- 右侧导航栏 -->
      <div :class="{ 'nav-open': isNavOpen, 'nav-closed':!isNavOpen }" class="instrument-nav">
        <button @click="toggleNav" class="nav-toggle">
          {{ isNavOpen ? '关闭导航' : '打开导航' }}
        </button>
        <div class="instrument-list">
          <img
            v-for="(instrument, instrumentIndex) in instruments"
            :key="instrumentIndex"
            :src="instrument.image"
            alt="Instrument"
            @click="selectedInstrument = instrument"
            class="instrument-item"
          >
        </div>
      </div>
    </div>
  </template>

<script>
export default {
  data () {
    return {
      lyrics: [
        {
          image: 'xnhrow1.png',
          imageOffset: -20,
          text: [
            { char: '新', offsetX: -141, playTime: 4.9, instrumentX: 303, instrumentY: 465 },
            { char: '年', offsetX: -152, playTime: 5.30, instrumentX: 345, instrumentY: 465 },
            { char: '好', offsetX: -153, playTime: 5.8, instrumentX: 397, instrumentY: 465 },
            { char: '呀', offsetX: -139, playTime: 6, instrumentX: 463, instrumentY: 465 },
            { char: '新', offsetX: -95, playTime: 6.41, instrumentX: 562, instrumentY: 465 },
            { char: '年', offsetX: -105, playTime: 7, instrumentX: 603, instrumentY: 465 },
            { char: '好', offsetX: -105, playTime: 7.45, instrumentX: 655, instrumentY: 465 },
            { char: '呀', offsetX: -93, playTime: 8, instrumentX: 720, instrumentY: 465 },
            { char: '祝', offsetX: -43, playTime: 8.07, instrumentX: 825, instrumentY: 465 },
            { char: '贺', offsetX: -50, playTime: 8.50, instrumentX: 870, instrumentY: 465 },
            { char: '大', offsetX: -53, playTime: 9, instrumentX: 920, instrumentY: 465 },
            { char: '家', offsetX: -42, playTime: 9.53, instrumentX: 985, instrumentY: 465 },
            { char: '新', offsetX: 10, playTime: 9.53, instrumentX: 1090, instrumentY: 465 },
            { char: '年', offsetX: -2, playTime: 9.53, instrumentX: 1130, instrumentY: 465 },
            { char: '好', offsetX: -3, playTime: 9.53, instrumentX: 1180, instrumentY: 465 }

          ],
          instruments: []
        },
        {
          image: 'xnhrow2.png',
          imageOffset: -20,
          text: [
            { char: '我', offsetX: -141, playTime: 13.19, instrumentX: 303, instrumentY: 760 },
            { char: '们', offsetX: -152, playTime: 13.71, instrumentX: 345, instrumentY: 760 },
            { char: '唱', offsetX: -153, playTime: 14.17, instrumentX: 397, instrumentY: 760 },
            { char: '歌', offsetX: -139, playTime: 14.33, instrumentX: 463, instrumentY: 760 },
            { char: '我', offsetX: -95, playTime: 14.77, instrumentX: 562, instrumentY: 760 },
            { char: '们', offsetX: -105, playTime: 15.34, instrumentX: 603, instrumentY: 760 },
            { char: '跳', offsetX: -105, playTime: 15.82, instrumentX: 655, instrumentY: 760 },
            { char: '舞', offsetX: -93, playTime: 16.21, instrumentX: 720, instrumentY: 760 },
            { char: '祝', offsetX: -43, playTime: 16.21, instrumentX: 825, instrumentY: 760 },
            { char: '贺', offsetX: -50, playTime: 16.21, instrumentX: 870, instrumentY: 760 },
            { char: '大', offsetX: -53, playTime: 16.21, instrumentX: 920, instrumentY: 760 },
            { char: '家', offsetX: -42, playTime: 16.21, instrumentX: 985, instrumentY: 760 },
            { char: '新', offsetX: 10, playTime: 16.21, instrumentX: 1090, instrumentY: 760 },
            { char: '年', offsetX: -2, playTime: 16.21, instrumentX: 1130, instrumentY: 760 },
            { char: '好', offsetX: -3, playTime: 16.21, instrumentX: 1180, instrumentY: 760 }

          ],
          instruments: []
        }

      ],
      instruments: [
        { image: 'guzhang.jpg' },
        { image: 'jiao.png' }
      ],
      selectedInstrument: null,
      isNavOpen: false,
      isPlaying: false,
      audio: new Audio('xnh.mp3'),
      currentTime: 0
    }
  },
  methods: {
    toggleNav () {
      this.isNavOpen = !this.isNavOpen
    },
    placeInstrument (lineIndex, charIndex) {
      if (this.selectedInstrument) {
        if (!this.lyrics[lineIndex].instruments) {
          this.lyrics[lineIndex].instruments = []
        }
        this.lyrics[lineIndex].instruments[charIndex] = this.selectedInstrument.image
        this.lyrics[lineIndex].instrumentAudios = this.lyrics[lineIndex].instrumentAudios || []
        this.lyrics[lineIndex].instrumentAudios[charIndex] = this.selectedInstrument.audio
      }
    },
    toggleMusic () {
      if (this.isPlaying) {
        this.audio.pause()
        clearInterval(this.timer)
      } else {
        this.audio.play()
        this.timer = setInterval(() => {
          this.currentTime = this.audio.currentTime
          this.checkInstrumentPlay()
        }, 100)
      }
      this.isPlaying = !this.isPlaying
    },
    checkInstrumentPlay () {
      this.lyrics.forEach((line, lineIndex) => {
        line.text.forEach((charObj, charIndex) => {
          if (
            line.instruments &&
              line.instruments[charIndex] &&
              line.instrumentAudios &&
              line.instrumentAudios[charIndex] &&
              this.currentTime >= charObj.playTime &&
              !charObj.hasPlayed
          ) {
            line.instrumentAudios[charIndex].play()
            charObj.hasPlayed = true
          }
        })
      })
    }
  },
  beforeDestroy () {
    clearInterval(this.timer)
  }
}
</script>

  <style scoped>
  .lyrics-container {
    font-family: "Courier New", Courier, monospace;
    line-height: 1.5;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
  }

  .play-music-button {
    margin-bottom: 20px;
    padding: 10px 20px;
    font-size: 16px;
    font-weight: 600;
    background: linear-gradient(135deg, #4a90e2 0%, #2b6cd4 100%);
    color: white;
    border: none;
    border-radius: 25px;
    cursor: pointer;
    box-shadow: 0 4px 15px rgba(43, 108, 212, 0.3);
    transition: all 0.3s ease;
    position: relative;
    overflow: hidden;
  }
  .play-music-button:hover {
    transform: translateY(-2px);
    box-shadow: 0 6px 20px rgba(43, 108, 212, 0.4);
  }

  .play-music-button:active {
    transform: translateY(1px);
  }
  .lyrics-content {
    width: 100%;
    max-width: 800px;
  }

  .row {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
  }

  .notation-image {
    width: 245px;
    max-width:  800px;
    margin-bottom: 10px;
    position: relative;
    left: 0;
  }

  .text {
    position: relative;
    letter-spacing: 0.5em;
    word-spacing: -0.4em;
    margin-left: 50px;
    display: flex;
    justify-content: center;
    white-space: pre;
    margin: 0;
  }

  .char {
    display: inline-block;
    min-width: 1em;
    position: relative;
    transition: transform 0.3s;
    color: #333;

  }
  .play-music-button::after {
    content: '';
    position: absolute;
    top: -50%;
    left: -50%;
    width: 200%;
    height: 200%;
    background: linear-gradient(
      45deg,
      transparent 25%,
      rgba(255, 255, 255, 0.1) 50%,
      transparent 75%
    );
    animation: shine 3s infinite;
  }
  .instrument-boxes {
    display: flex;
    justify-content: center;
    margin-top: 10px;
  }

  .instrument-box {
    width: 6px; /* 减小宽度 */
    height: 6px; /* 减小高度 */
    border: 1px solid #ccc;
    margin: 0 3px; /* 减小间距 */
    display: flex;
    background: rgba(255, 255, 255, 0.9);
    transition: all 0.2s ease;
    box-shadow: 0 2px 8px rgba(74, 144, 226, 0.15);

    justify-content: center;
    align-items: center;
    cursor: pointer;
    position: absolute;  /* 关键：让方框可以自由定位 */
  }
  .instrument-box:hover {
    background: #4a90e2;
    transform: scale(1.2);
  }
  .instrument-image {
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
  }

  .instrument-nav {
    position: fixed;
    top: 0;
    right: -100px; /* 减小导航栏宽度 */
    width: 100px; /* 减小导航栏宽度 */
    height: 100vh;
    background-color: #f4f4f4;
    transition: right 0.3s;
    padding: 10px; /* 减小内边距 */
    box-shadow: -2px 0 5px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column; /* 竖列排列 */
    align-items: center; /* 水平居中 */
    background: rgba(255, 255, 255, 0.95);
    backdrop-filter: blur(10px);
    border-left: 1px solid rgba(0, 0, 0, 0.1);
    box-shadow: -5px 0 25px rgba(0, 0, 0, 0.08);
  }

  .nav-open {
    right: 0;
  }

  .nav-closed {
    right: -100px; /* 与导航栏宽度一致 */
  }

  .nav-toggle {
    background: #ffffff;
    color: #4a90e2;
    border: 2px solid #4a90e2;
    border-radius: 15px;
    font-weight: 500;
    transition: all 0.3s ease;
    box-shadow: 0 2px 8px rgba(74, 144, 226, 0.2);
    position: absolute;
    top: 10px;
    left: -35px;  /* 调整位置 */
    padding: 5px 8px;  /* 减小内边距 */
    font-size: 12px;  /* 缩小字体 */
    cursor: pointer;
    white-space: nowrap;  /* 防止文字换行 */
  }
  .nav-toggle:hover {
    background: #4a90e2;
    color: white;
    transform: translateX(5px);
  }
  .instrument-list {
    margin-top: 40px;
    display: flex;
    flex-direction: column; /* 竖列排列 */
    align-items: center; /* 水平居中 */
    max-height: calc(100vh - 60px); /* 留出顶部按钮的空间 */
    overflow-y: auto; /* 添加垂直滚动条 */
  }

  .instrument-item {
    width: 40px;
    height: 40px;
    border-radius: 12px;
    background: white;
    padding: 5px;
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.08);
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    border: 2px solid transparent;
  }
  .instrument-item:hover {
    transform: scale(1.15);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.12);
    border-color: #4a90e2;
  }
  </style>
