<template>
    <div class="container">
      <!-- 背景音乐控制 -->
      <div class="music-control" @click="togglePlay">
        <span :class="['icon', { playing: isBgPlaying }]">🔊</span>
      </div>

      <h1 class="section-title">
        <span class="highlight">八分音符的时值关系</span>
      </h1>

      <!-- 音频组1 -->
      <div class="audio-group">
        <div class="text-score-wrapper">
          <p>4/2 <u>X0</u> <u>X0</u> | <u>0X</u> x | <u>X0</u> <u>X0</u> | <u>0X</u> X ||</p>
        </div>
        <div class="audio-control-group">
          <div class="speed-control">
            <label>播放速度：</label>
            <input
              type="range"
              min="0.5"
              max="2"
              step="0.1"
              v-model="audio1Speed"
              @input="setPlaybackSpeed(1, $event)"
            >
            <span>{{ audio1Speed }}x</span>
          </div>
          <div class="audio-wrapper">
            <audio ref="audio1" controls>
              <source src="cspd1.mp3" type="audio/mpeg">
              您的浏览器不支持音频播放
            </audio>
          </div>
        </div>
      </div>

      <!-- 音频组2 -->
      <div class="audio-group">
        <div class="text-score-wrapper">
          <p>4/2 <u>0X</u> <u>0X</u> | <u>X0</u> x | <u>0X</u> <u>0X</u> | <u>X0</u> X ||</p>
        </div>
        <div class="audio-control-group">
          <div class="speed-control">
            <label>播放速度：</label>
            <input
              type="range"
              min="0.5"
              max="2"
              step="0.1"
              v-model="audio2Speed"
              @input="setPlaybackSpeed(2, $event)"
            >
            <span>{{ audio2Speed }}x</span>
          </div>
          <div class="audio-wrapper">
            <audio ref="audio2" controls>
              <source src="cspd2.mp3" type="audio/mpeg">
              您的浏览器不支持音频播放
            </audio>
          </div>
        </div>
      </div>

      <!-- 背景音乐 -->
      <audio ref="bgAudio" loop>
        <source src="csyy.mp3" type="audio/mpeg">
        您的浏览器不支持音频播放
      </audio>
    </div>
  </template>

<script>
export default {
  data () {
    return {
      isBgPlaying: false,
      audio1Speed: 1.0,
      audio2Speed: 1.0
    }
  },
  methods: {
    togglePlay () {
      const audio = this.$refs.bgAudio
      if (this.isBgPlaying) {
        audio.pause()
      } else {
        audio.play()
      }
      this.isBgPlaying = !this.isBgPlaying
    },
    setPlaybackSpeed (audioNumber, event) {
      const audioRef = `audio${audioNumber}`
      const audioElement = this.$refs[audioRef]
      if (audioElement) {
        audioElement.playbackRate = parseFloat(event.target.value)
      }
    }
  }
}
</script>

  <style>
  /* 新增速度控制样式 */
.speed-control {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
  background: #ffffff;
  padding: 10px;
  border-radius: 6px;
}

.speed-control label {
  font-size: 14px;
  color: #4a90e2;
  font-weight: 500;
}

.speed-control input[type="range"] {
  width: 120px;
  height: 4px;
  background: #dee2e6;
  border-radius: 2px;
  outline: none;
}

.speed-control input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 16px;
  height: 16px;
  background: #4a90e2;
  border-radius: 50%;
  cursor: pointer;
}

.speed-control span {
  font-size: 14px;
  color: #495057;
  min-width: 40px;
}

.audio-control-group {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
  /* 新增音乐控制样式 */
.music-control {
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 1000;
  cursor: pointer;
  background: rgba(255, 255, 255, 0.9);
  border-radius: 50%;
  padding: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease;
}

.music-control:hover {
  transform: scale(1.1);
  background: rgba(74, 144, 226, 0.9);
}

.music-control:hover .icon {
  color: white;
}

.icon {
  font-size: 24px;
  color: #4a90e2;
  display: block;
  transition: color 0.3s ease;
}

.icon.playing {
  animation: pulse 1s infinite;
}

@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.1); }
  100% { transform: scale(1); }
}

  .section-title {
    text-align: center;
    margin: 2rem 0 3rem;
    position: relative;
    font-family: 'Helvetica Neue', Arial, sans-serif;
  }

  .highlight {
    display: inline-block;
    position: relative;
    font-size: 2.2rem;
    color: #2c3e50;
    padding: 0 1.5rem;
    line-height: 1.3;
    transition: all 0.3s ease;
  }

  .highlight::before {
    content: "";
    position: absolute;
    bottom: -8px;
    left: 50%;
    transform: translateX(-50%);
    width: 60px;
    height: 3px;
    background: #4a90e2;
    border-radius: 2px;
  }

  .highlight::after {
    content: "";
    position: absolute;
    bottom: -12px;
    left: 50%;
    transform: translateX(-50%);
    width: 40px;
    height: 2px;
    background: rgba(74, 144, 226, 0.4);
  }

  .section-title:hover .highlight {
    color: #34495e;
    transform: scale(1.02);
  }

  /* 响应式调整 */
  @media (max-width: 768px) {
    .highlight {
        font-size: 1.8rem;
        padding: 0 1rem;
    }

    .highlight::before {
        width: 40px;
        bottom: -6px;
    }

    .highlight::after {
        width: 30px;
        bottom: -10px;
    }
  }
  /* 基础重置 */
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

  /* 容器样式 */
  .container {
    max-width: 800px;
    margin: 2rem auto;
    padding: 0 20px;
  }

  /* 音频组样式 */
  .audio-group {
    background: #f8f9fa;
    border-radius: 12px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    padding: 20px;
    margin-bottom: 30px;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }

  .audio-group:hover {
    transform: translateY(-2px);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
  }

  /* 文字谱子样式 */
  .text-score-wrapper {
    background: #ffffff;
    border-radius: 8px;

    padding: 40px;
    margin-bottom: 10px;
  }

  /* 文字间隔调整 */
  p {
    letter-spacing: 1px; /* 调整这个值来改变文字间隔 */
  }

  /* 音频控件美化 */
  .audio-wrapper {
    background: #ffffff;
    border-radius: 8px;
    padding: 15px;
  }

  audio {
    width: 100%;
    max-width: 600px;
  }

  /* 自定义音频控件样式 */
  audio::-webkit-media-controls-panel {
    background-color: #e9ecef;
  }

  audio::-webkit-media-controls-play-button {
    background-color: #4a90e2;
    border-radius: 50%;
  }

  audio::-webkit-media-controls-timeline {
    background-color: #dee2e6;
    border-radius: 25px;
    margin: 0 10px;
  }

  audio::-webkit-media-controls-current-time-display,
  audio::-webkit-media-controls-time-remaining-display {
    color: #495057;
    font-family: 'Segoe UI', sans-serif;
  }

  /* 响应式设计 */
  @media (max-width: 768px) {
    .container {
        padding: 0 10px;
    }

    .audio-group {
        padding: 15px;
    }
  }
  </style>
