<template>
    <div class="container">
        <div class="image-display" v-if="currentImage">
  <img :src="currentImage" alt="动作图" />
</div>
      <!-- 背景音乐控制 -->
      <div class="music-control" @click="togglePlay">
        <span :class="['icon', { playing: isBgPlaying }]">🔊</span>
      </div>

      <h1 class="section-title">
        <span class="highlight">这是一首3/4拍的歌曲，他的节拍和以前所学的2/4拍的歌曲有什么不同？</span>
      </h1>
      <img alt="xf" src="xfps.jpg">
      <!-- 背景音乐 -->
      <audio ref="bgAudio" loop>
        <source src="xf.mp3" type="audio/mpeg">
        您的浏览器不支持音频播放
      </audio>
    </div>
  </template>

<script>
export default {
  data () {
    return {
      isBgPlaying: false,
      audio1Speed: 1.0,
      audio2Speed: 1.0,
      currentImage: null,
      timer: null,
      imageTimings: [
        { src: 'xz.png', start: 9, end: 10.70 }, // 走路
        { src: 'ps.png', start: 10.7, end: 12.6 }, // 抬手
        { src: 'xz.png', start: 12.6, end: 14.49 }, // 蹲下
        { src: 'ps.png', start: 14.49, end: 16.37 }, // 蹲下

        { src: 'xz.png', start: 16.37, end: 22 }, // 蹲下
        { src: 'ps.png', start: 22, end: 24 }, // 蹲下
        { src: 'xz.png', start: 24, end: 25.75 }, // 蹲下
        { src: 'dj.png', start: 25.75, end: 27.57 }, // 蹲下
        { src: 'xz.png', start: 27.57, end: 29.5 }, // 蹲下
        { src: 'dj.png', start: 29.5, end: 31.36 }, // 蹲下
        { src: 'xz.png', start: 31.37, end: 37 }, // 蹲下
        { src: 'dj.png', start: 37, end: 46.41 }, // 蹲下
        { src: 'xz.png', start: 46.41, end: 48.41 }, // 蹲下
        { src: 'pj.png', start: 48.41, end: 50.10 }, // 蹲下
        { src: 'xz.png', start: 50.10, end: 52 }, // 蹲下
        { src: 'pj.png', start: 52, end: 53.80 }, // 蹲下
        { src: 'xz.png', start: 53.80, end: 59.52 }, // 蹲下
        { src: 'pj.png', start: 59.52, end: 61.52 } // 蹲下

      ]
    }
  },
  methods: {
    togglePlay () {
      const audio = this.$refs.bgAudio
      if (this.isBgPlaying) {
        audio.pause()
        clearInterval(this.timer)
        this.currentImage = null
      } else {
        audio.play()
        this.trackImageByAudioTime()
      }
      this.isBgPlaying = !this.isBgPlaying
    },
    setPlaybackSpeed (audioNumber, event) {
      const audioRef = `audio${audioNumber}`
      const audioElement = this.$refs[audioRef]
      if (audioElement) {
        audioElement.playbackRate = parseFloat(event.target.value)
      }
    },
    trackImageByAudioTime () {
      const audio = this.$refs.bgAudio
      this.timer = setInterval(() => {
        const currentTime = audio.currentTime
        const matched = this.imageTimings.find(img => currentTime >= img.start && currentTime < img.end)
        this.currentImage = matched ? matched.src : null
      }, 200) // 每200毫秒检测一次
    }
  }
}
</script>

  <style>
.image-display {
  position: fixed;
  top: 20px;
  left: 20px;
  width: 100px;
  height: 100px;
  z-index: 999;
}

.image-display img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0,0,0,0.2);
}
  /* 新增音乐控制样式 */
.music-control {
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 1000;
  cursor: pointer;
  background: rgba(255, 255, 255, 0.9);
  border-radius: 50%;
  padding: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease;
}

.music-control:hover {
  transform: scale(1.1);
  background: rgba(74, 144, 226, 0.9);
}

.music-control:hover .icon {
  color: white;
}

.icon {
  font-size: 24px;
  color: #4a90e2;
  display: block;
  transition: color 0.3s ease;
}

.icon.playing {
  animation: pulse 1s infinite;
}

@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.1); }
  100% { transform: scale(1); }
}

  .section-title {
    text-align: center;
    margin: 2rem 0 3rem;
    position: relative;
    font-family: 'Helvetica Neue', Arial, sans-serif;
  }

  .highlight {
    display: inline-block;
    position: relative;
    font-size: 2.2rem;
    color: #2c3e50;
    padding: 0 1.5rem;
    line-height: 1.3;
    transition: all 0.3s ease;
  }

  .highlight::before {
    content: "";
    position: absolute;
    bottom: -8px;
    left: 50%;
    transform: translateX(-50%);
    width: 60px;
    height: 3px;
    background: #4a90e2;
    border-radius: 2px;
  }

  .highlight::after {
    content: "";
    position: absolute;
    bottom: -12px;
    left: 50%;
    transform: translateX(-50%);
    width: 40px;
    height: 2px;
    background: rgba(74, 144, 226, 0.4);
  }

  .section-title:hover .highlight {
    color: #34495e;
    transform: scale(1.02);
  }

  /* 响应式调整 */
  @media (max-width: 768px) {
    .highlight {
        font-size: 1.8rem;
        padding: 0 1rem;
    }

    .highlight::before {
        width: 40px;
        bottom: -6px;
    }

    .highlight::after {
        width: 30px;
        bottom: -10px;
    }
  }
  /* 基础重置 */
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

  /* 容器样式 */
  .container {
    max-width: 800px;
    margin: 2rem auto;
    padding: 0 20px;
  }

  /* 音频组样式 */
  .audio-group {
    background: #f8f9fa;
    border-radius: 12px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    padding: 20px;
    margin-bottom: 30px;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }

  .audio-group:hover {
    transform: translateY(-2px);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
  }

  /* 文字谱子样式 */
  .text-score-wrapper {
    background: #ffffff;
    border-radius: 8px;
    padding: 50px;
    margin-bottom: 10px;
  }
  .text-score-wrapper p {
  font-size: 20px;  /* 调整这个数值控制字体大小 */
  letter-spacing: 1px;
}
  /* 文字间隔调整 */
  p {
    letter-spacing: 1px; /* 调整这个值来改变文字间隔 */
  }

  /* 音频控件美化 */
  .audio-wrapper {
    background: #ffffff;
    border-radius: 8px;
    padding: 15px;
  }

  audio {
    width: 100%;
    max-width: 600px;
  }

  /* 自定义音频控件样式 */
  audio::-webkit-media-controls-panel {
    background-color: #e9ecef;
  }

  audio::-webkit-media-controls-play-button {
    background-color: #4a90e2;
    border-radius: 50%;
  }

  audio::-webkit-media-controls-timeline {
    background-color: #dee2e6;
    border-radius: 25px;
    margin: 0 10px;
  }

  audio::-webkit-media-controls-current-time-display,
  audio::-webkit-media-controls-time-remaining-display {
    color: #495057;
    font-family: 'Segoe UI', sans-serif;
  }

  /* 响应式设计 */
  @media (max-width: 768px) {
    .container {
        padding: 0 10px;
    }

    .audio-group {
        padding: 15px;
    }
  }
  </style>
